import React, { useState, useEffect } from "react";
import ReactPaginate from "react-paginate";

interface Props {
  page: number;
  info: any;
  setPage: any;
}

export const Pagination: React.FC<Props> = ({ page, info, setPage }) => {
  let pageChange = (data: { selected: number; }) => {
    setPage(data.selected + 1);
  };

  const [width, setWidth] = useState(window.innerWidth);
  const updateDimensions = () => {
    setWidth(window.innerWidth);
  };
  useEffect(() => {
    window.addEventListener("resize", updateDimensions);
    return () => window.removeEventListener("resize", updateDimensions);
  }, []);

  return (
    <>
      <style>
        {`
          @media (max-width: 768px) {
            .pagination {
              font-size: 12px;
            }
            .next,
            .prev {
              display: none;
            }
          }
          @media (max-width: 768px) {
            .pagination {
              font-size: 14px;
            }
          }
        `}
      </style>
      <ReactPaginate
        className="pagination justify-content-center my-4 gap-1"
        nextLabel="Next"
        forcePage={page === 1 ? 0 : page - 1}
        previousLabel="Prev"
        previousClassName="btn btn-primary fs-10 prev text-bg-secondary"
        nextClassName="btn btn-primary fs-10 next text-bg-secondary"
        activeClassName="active text-bg-secondary"
        marginPagesDisplayed={width < 576 ? 1 : 2}
        pageRangeDisplayed={width < 576 ? 1 : 2}
        pageCount={info?.pages}
        onPageChange={pageChange}
        pageClassName="page-item text-bg-secondary"
        pageLinkClassName="page-link text-bg-secondary"
      />
    </>
  );
};
