import React, { useEffect, useState } from 'react';
import { Character } from './Character';
import { HashLoader } from 'react-spinners';
import { Pagination } from '../Pagination';
import { Link, useParams } from 'react-router-dom';
import { Card } from '../Card';

interface CharacterType {
  id: number;
  image: string;
  name: string;
}

export const Characters: React.FC<{}> = () => {
  const [characters, setCharacters] = useState<any>(null);
  const [page, setPage] = useState<number>(1)
  const { characterId = 0 } = useParams();


  useEffect(() => {
    fetch(`https://rickandmortyapi.com/api/character/?page=${page}`)
      .then((response) => response.json())
      .then((data) => {
        setCharacters(data);
      })
      .catch((err) => {
        console.log(err.message);
      });
  }, [page]);

  return (
    (+characterId !== 0 && characters)
      ? (
        <div 
          className="container d-flex justify-content-center align-items-center"
          style={{height: '100vh'}}
        >
          <Character character={characters.results
            .filter((character: CharacterType) => {
              return character.id === +characterId
            })[0]
          } />
        </div>
      )
      : (<div>
        {characters ? (
          (<div className="container text-center p-5">
            <ul className="row row-cols-xs-1 row-cols-md-2 row-cols-lg-3 g-5 p-0">
              {characters.results.map((character: CharacterType) => (
                <li key={character.id} className="col p-0">
                  <Link
                    to={`${character.id}`}
                    style={{textDecoration: 'none'}}
                    className="d-flex justify-content-center align-items-center"
                  >
                    <Character character={character} />
                  </Link>
                </li>
              ))}
            </ul>
            <Pagination page={page} setPage={setPage} info={characters.info} />
          </div>)
        ) : (
          <HashLoader
            color="aqua"
            size={100}
            cssOverride={{
              display: 'block',
              position: 'fixed',
              transform: 'translate(-50%, -50%)',
              top: '50%',
              left: '50%',
            }}
          />
        )}
      </div>)
  );
};