import { useEffect, useState } from "react";

export const Episodes = () => {
  const [episodes, setEpisodes] = useState();

  useEffect(() => {
    fetch(`https://rickandmortyapi.com/api/location`)
      .then((response) => response.json())
      .then((data) => {
        setEpisodes(data);
        console.log(data)
      })
      .catch((err) => {
        console.log(err.message);
      });
  }, []);

  return (
    <div>Episodes</div>
  )
}